.natayejbox{
    grid-column: 1 / 7;
    


    grid-gap: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;


    border: 2px solid #4987D8;
    padding: 1rem;

}


@media only screen and (max-width: 700px) {
    .natayejbox {
        grid-template-columns:1fr 1fr;
    }
  }